export const StarRating = ({
	rating,
	size = "sm",
	className,
}: StarRatingProps) => {
	const sizes = {
		xs: "text-base",
		sm: "text-xl",
		md: "text-2xl",
	};

	// Trata rating 0 como estrelas vazias
	const normalizedRating = rating || 0;

	return (
		<div className={`flex items-center ${className}`}>
			{[1, 2, 3, 4, 5].map((star) => {
				const isFilled = normalizedRating >= star;
				const isHalf =
					normalizedRating >= star - 0.5 && normalizedRating < star;

				return (
					<span key={star} className={`${sizes[size]} mx-0.5`}>
						{isFilled ? (
							<FaStar className="text-yellow-400" />
						) : isHalf ? (
							<FaRegStarHalf className="text-yellow-400" />
						) : (
							<FaRegStar className="text-gray-300 dark:text-gray-500" />
						)}
					</span>
				);
			})}
		</div>
	);
};
